import NewsItem from '@/components/NewsItem'
import { mapActions, mapGetters } from 'vuex'
import api from '@/utils/api'
import qs from 'qs'

export default {
	async asyncData({store, route}){
		let limit = 6
		let link = api.published
		let page = parseInt(route.params.page)
		let params = {
			limit,
			offset: (page-1)*limit
		}
		link+='?'+qs.stringify(params)
		await store.dispatch("news/FETCH_NEWS",link)
		return { page }
	},
	data(){
		return {
			page: 1
		}
	},
	components: {
		NewsItem
	},
	computed: {
		...mapGetters('news', ['NEWS', 'PREV', 'NEXT']),
	},
	methods: {
		...mapActions('news', ['FETCH_NEWS']),
		async next(){
			await this.FETCH_NEWS(this.NEXT)
			this.page+=1
			this.$router.push(`/page/${this.page}`)
		},
		async prev(){
			await this.FETCH_NEWS(this.PREV)
			this.page-=1
			this.$router.push(`/page/${this.page}`)
		}
	}
}
